.lightbox-modal {
  --size: 1.85rem;
  --field-size: calc(var(--size) * 1.5);
  --offset: .625rem;
  --button-controll-size: 4.375rem;
  padding: 0 var(--offset);
  background-color: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.lightbox-modal.active {
  display: flex;
}

.lightbox-modal-close-button {
  font-size: var(--size);
  width: var(--field-size);
  height: var(--field-size);
  top: var(--offset);
  right: var(--offset);
  color: #fff;
  cursor: pointer;
  background: none;
  font-weight: bold;
  position: absolute;
}

.lightbox-modal-content {
  justify-content: center;
  align-items: center;
  display: flex;
}

.lightbox-modal-q-info {
  top: var(--offset);
  left: var(--offset);
  color: #fff;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 1.2rem;
  position: absolute;
}

.lightbox-modal-img {
  width: 75%;
  height: 85%;
}

.lightbox-controlls {
  width: 100%;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.lightbox-controll-button {
  width: var(--button-controll-size);
  height: calc(var(--button-controll-size) * 4);
  color: #fff;
  opacity: 0;
  background: rgba(220, 220, 220, .2);
  border: none;
  font-size: 3rem;
  font-weight: bold;
  transition: opacity .3s;
}

.lightbox-controll-button:hover {
  opacity: 1;
}

